	module.exports = {"data":{"markdownRemark":{"html":"<p>Finally Nightly (Weekly?) Build specific flavor has come out ... .... A new build of Windows 10 Insider Preview, 14915 was coming so install before going to bed. When I woke up in the morning, wireless LAN ceased to say totally. The problematic PC is <a href=\"/surface-pro\">Microsoft Surface Pro</a> .</p>\n<h2>What you did</h2>\n<ol>\n<li>Delete the driver and force reinstall → No effect</li>\n<li>Two button shutdown → No effect</li>\n</ol>\n<p><strong><a href=\"http://betanews.com/2016/09/01/insider-preview-build-14915-kills-wifi/\">Windows 10 Insider Preview Build 14915 killing Wi-Fi for some users, and there's no fix - betanews</a></strong></p>\n<p>It seems that the update is still causing trouble. Since it seems that wired connection via USB can be done, it is going to wait for a modified update while deceiving and deceptive operation.</p>\n<h2>Additional notes</h2>\n<p>Although the report was delayed, it seems that the Wi - Fi connection has improved at 14926. Instead, the display of the start menu has gone wrong, but ... let's write it.</p>","frontmatter":{"cover":{"childImageSharp":{"responsiveSizes":{"src":"/danmaq.gatsby/static/2016-windows10-insider-problem-5176fbd7942cd47afee39f8bec19c0f5-66952.jpg","srcSet":"/danmaq.gatsby/static/2016-windows10-insider-problem-5176fbd7942cd47afee39f8bec19c0f5-db268.jpg 200w,\n/danmaq.gatsby/static/2016-windows10-insider-problem-5176fbd7942cd47afee39f8bec19c0f5-fed06.jpg 400w,\n/danmaq.gatsby/static/2016-windows10-insider-problem-5176fbd7942cd47afee39f8bec19c0f5-66952.jpg 401w","sizes":"(max-width: 401px) 100vw, 401px"}}},"date":"2016-09-02T09:04:38+09:00","strDate":"2016/9/2","title":"Windows 10 build 14915, Wi-Fi will die"},"fields":{"langKey":"en","slug":"/en/blog/2016/09/02-windows10-build-14915、wi-fiが死ぬ不具合/"}}},"pathContext":{"path":"/en/blog/2016/09/02-windows10-build-14915、wi-fiが死ぬ不具合/","langKey":"en"}}

